import Layout from "./components/pagination/Layout/Layout";

function App() {
  let { user } = useSelector((state) => state.auth);
  let routes = createBrowserRouter([
    {
      path: "",
      element: <Layout />,
      children: [
        {
          index: true,
          element: <HomePage />,
        },
        {
          path: "/login",
          element: <Login />,
        },
        {
          path: "/create-post",
          element:<CreatePost/>,
        },
        { path: "/register", element: <Register /> },
        { path: "/posts", element: <PostsPage /> },
        {path:"*", element:<h1 >Page not found 404</h1>},
        {
          path: "admin-dashboard",
          children: [
            {
              index: true,
              element: <AdminDashboard />,
            },
           
          ],
        },
      ],
    },
    
  ]);

  return (
    <div>
      <Userpostcontext>
        <RouterProvider router={routes}>
          <Layout />
        </RouterProvider>
      </Userpostcontext>
      <Footer />
    </div>
  );
}

export default App;
